  display: inline-block;
  width: 116px;
  flex-shrink: 0;
}
.button1 {
  border-radius: var(--br-31xl);
  background-color: var(--color-white);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: var(--padding-5xl) var(--padding-29xl);
  cursor: pointer;
  text-align: center;
  color: var(--color-gray);
}
.text1 {
  position: absolute;
  top: 467px;
  left: calc(50% - 179.5px);
  width: 359px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.projects-child {
  border: 0;
  position: absolute;
  top: 53px;
  left: 44px;
  border-radius: 30px;
  background-color: #d9d9d9;
  width: 468px;
  height: 924px;
}
.projects {
  position: relative;
  background-color: var(--color-darkslategray);
  width: 100%;
  height: 1185px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-poppins);
}
